import React, { Component } from "react";
import { fabric } from "fabric";
import storyPicture from "../img/story.png";
import Menu from "../Menu";

class Story extends Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
  }

  componentDidMount() {
    var canvas = new fabric.Canvas("c", {
      selection: false,
      hoverCursor: "default"
    });
    canvas.setDimensions({
      width: 1080,
      height: 1920
    });
    fabric.Image.fromURL(storyPicture, oImg => {
      oImg.set({ selectable: false, selection: false });
      canvas.add(oImg);
      canvas.centerObject(oImg);
      oImg.sendBackwards();
    });

    var text = new fabric.Text("", {
      selectable: false,
      dirty: true,
      textAlign: "center",
      fill: "#fff",
      lineHeight: 1,
      fontFamily: "Lato",
      fontWeight: 300,
      fontSize: 48
    });
    canvas.add(text);
    canvas.centerObject(text);

    document.getElementById("c").fabric = canvas;
  }

  componentDidUpdate() {
    this.updateCanvas();
  }

  updateCanvas = () => {
    const canvas = document.querySelector("#c").fabric;
    canvas.forEachObject(o => {
      if (o.get("type") === "text") {
        o.text = this.state.value;
        o.set({
          originX: "center"
        });
        canvas.centerObjectV(o);
        canvas.renderAll();
      }
    });
  };

  download = () => {
    const canvas = document.querySelector("#c").fabric;
    // window.open(canvas.toDataURL("png"));
    document.querySelector("#link").href = canvas.toDataURL("image/jpg");
    document.querySelector("#link").click();
  };

  render() {
    return (
      <div className="container container-story">
        <Menu />
        <canvas id="c" />
        <div className="form">
          <textarea
            value={this.state.value}
            onChange={e =>
              this.setState({ value: e.target.value.toUpperCase() })
            }
            rows="10"
            placeholder="Contenu de la story"
          />
          <button className="submit" onClick={() => this.download()}>
            Télécharger
          </button>
          <a href="" id="link" download="story-insta.jpg" />
        </div>
        {/* <img src={storyPicture} id="my-image" /> */}
      </div>
    );
  }
}

export default Story;
